<!-- 本页面属于 维修单详情的 检定状态 已经检定完毕 status === 90 || 91 -->
<template>
  <view class="finished padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="维修单详情"></u-navbar>

    <view class="margin-normal bg-white padding-normal radius-normal" @click="foMaintProcess">
      <u-steps
          mode="number"
          :active-color="'#24A27C'"
          :vice-active-color="detail.status === '91'? '#F4516C': '#24A27C'"
          :viceicon="detail.status === '91'? 'close': ''"
          :list="numList"
          :current="current"></u-steps>
    </view>

    <view v-if="detail && !empty">
      <view>
        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="工单编号"
              :detailContent="detail.seqNo"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="创建时间"
              :detailContent="detail.createTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="最近更新"
              :detailContent="detail.updateTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="当前状态"
              :detailContent="detail.statusText"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>

        </nnkjc-detail-group>

        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="故障原因"
              :detailContent="detail.content"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>
        </nnkjc-detail-group>

        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="维修人"
              :detailContent="detail.maintStaffName"
              :align="'left'"
          >
          </nnkjc-detail-item>
        </nnkjc-detail-group>

        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="维修检定"
              :detailContent="detail.verifyContent || ' '"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>

      <!--  查看维修记录 -->
      <view>
        <view v-show="showBtn2" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple :loading="loading" plain @click="forMaintView">
            <text style="color: #24a27c;">查看维修记录</text>
          </u-button>
        </view>
      </view>

    </view>

    <u-toast ref="uToast"/>
  </view>
</template>

<script>
  export default {
    name: "checked",
    data() {
      return {
        loading: false,
        showBtn2: true,
        background: {
          'background': '#f2f2f8'
        },
        loading: false,
        empty: false,
        detail: {
          seqNo: "",
          status: "",
          createTime: "",
          updateTime: "",
          statusText: ""
        },
        searchParams: {
          id: "",
          type: "",
          status: ""
        },
        numList: [{
          name: '新建'
        }, {
          name: '委派'
        }, {
          name: '维修'
        }, {
          name: '检定'
        },],
        current: 3,
      }
    },
    computed: {
      PREMISS() {
        return this.$store.state.PREMISS
      }
    },
    onLoad(options) {
      this.searchParams.id = options.taskId
      this.searchParams.status = options.status
      this.searchParams.type = options.type
    },
    onReady() {
      this.getDetail();
    },
    methods: {
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      getDetail() {
        this.$u
            .post("/maint/getWeixiuTaskDetail", this.searchParams)
            .then(res => {
              if (res.data) {
                const user = uni.getStorageSync("user")

                const premissObj = uni.getStorageSync("premissObj")
                this.detail = res.data;
                if (this.detail) {

                  //  状态是 90 91 && 有查看记录的权限
                  this.showBtn2 = !!(premissObj && premissObj[this.PREMISS.viewMaintRecord])

                  const res = [{
                    "表单状态": this.detail.status,
                    "表单 mstaffId": this.detail.maintStaffId,
                    "用户 staffId": user.staffId,
                    "表单类型": this.detail.type,
                    "查看记录权限": premissObj[this.PREMISS.viewMaintRecord],
                  }];

                  console.table(res);
                }
              } else {
                this.empty = true;
              }
            })
            .catch(err => {
            });
      },
      foMaintProcess() {
        const params = this.$u.queryParams({
          taskId: this.searchParams.id,
        })

        uni.navigateTo({
          url: "/pages/maint-process/index" + params
        })
      },
      forMaintView() {
        const params = this.$u.queryParams({
          taskId: this.searchParams.id,
        })
        uni.navigateTo({
          url: "/pages/make-maint-record/maint-record-view" + params
        })
      },
    }
  }
</script>

